import React, { useEffect, useState } from 'react';
import { Route, Switch } from 'react-router-dom';
import { useTranslation} from 'react-i18next';

import StratosphereApp from '../src/app/component/stratosphere-app/stratosphere-app';
import ToastNotification from './app/component/shared/toast-notifications/toast-notifications';
import getStratosphereErrorPaths from './app/component/stratosphere-error-paths/get-stratosphere-error-paths';
import Footer from './app/component/shared/footer/footer';

import '@patternfly/react-core/dist/styles/base.css';
import '@patternfly/patternfly/patternfly.css';
import './App.scss';
import ErrorNotification from './app/component/stratosphere-notifications/error-notification';
import { useChrome } from '@redhat-cloud-services/frontend-components/useChrome';

function App() {
    // for passing toast notifications
    const [toast, setToast] = useState({});
    const chrome = useChrome();
    const { t, i18n } = useTranslation();

    useEffect(() => {
        chrome.updateDocumentTitle('connect');
        window.history.pushState(null, null, window.location.href);
        window.onpopstate = function () {
            window.history.go(1);
        };

        // unregister as a cleanup step for this component
        const unregister = chrome.on('APP_NAVIGATION', (event) => history.push(`/${event.navId}`));
        return () => {
            unregister();
        };
    }, []); // no dependencies, will not re-run after first render

    return (
        <>
            <ToastNotification toastData={toast} />
            <Switch>
                <Route exact path="/">
                    <ErrorNotification local={t} type="default-error" />
                </Route>
                <Route
                    path="/:partnerId"
                    // must render this way so the `partnerId` match can be injected via props
                    render={(props) => (
                        <StratosphereApp
                            local={t}
                            i18n={i18n}
                            useToast={setToast}
                            getErrorPaths={getStratosphereErrorPaths}
                            {...props}
                        />
                    )}
                />
            </Switch>
            <Footer local={t} />
        </>
    );
}

export default App;
