import {
  ActionGroup,
  Button,
  DescriptionList,
  DescriptionListDescription,
  DescriptionListGroup,
  DescriptionListTerm,
  Form,
  PageSection,
  Spinner,
  Title,
} from '@patternfly/react-core';
import PropTypes from 'prop-types';
import React from 'react';
import { Redirect } from 'react-router-dom';
import useChrome from '@redhat-cloud-services/frontend-components/useChrome';
import PartnerOrderDetails from '../order-details/partner-order-details';
import getStratosphereErrorPaths from '../stratosphere-error-paths/get-stratosphere-error-paths';
import { getDefaultTerms, TermsFormGroup } from './terms-form-group';


// fake for local testing by changing this import to "...from '../bff-service/bff-service-fake'"
import { getPartnerOrderDetailsAsync, linkPartnerOrderToAccountAsync } from '../bff-service/bff-service';

class ReviewLinkAccount extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      mergedResponse: null,
      redirect: null,
      // form contents
      hideTerms: false,
      isAgree: false,
      termsPdfId: null,
      // concurrent request control
      submitting: false,
      fetchingResponse: false,
    };

    this.abortController = new AbortController();
  }

  render() {
    const { redirect, mergedResponse } = this.state;
    const { partnerId, local, analytics } = this.props;

    let accountDetails = {};

    if (mergedResponse) {
      if ('aws' === partnerId) {
        accountDetails.awsAccountId = mergedResponse.marketplaceDetails.customerAccountId;
      } else if ('azure' === partnerId) {
        accountDetails.azureAccountId = mergedResponse.marketplaceDetails.azureCustomerId;
      } else if ('gcp' === partnerId) {
        accountDetails.gcpAccountId = mergedResponse.marketplaceDetails.gcpAccountId;
      }

      if (mergedResponse.redHatAccountDetails.accountNumber) {
        accountDetails.rhAccountNumber = mergedResponse.redHatAccountDetails.accountNumber;
      } else {
        accountDetails.rhAccountNumber = mergedResponse.redHatAccountDetails.company;
      }
      console.debug('* accountDetails : ', accountDetails);
    }

    if (redirect) {

      const info = {
        outcome:'error',
        partner: this.props.partnerId,
        errorType: redirect //this has the redirect uri value equivalent to the error type
      }
      try{
        if(accountDetails.linkedRedHatAccountId){

          info.outcome = 'already-linked';
          info.rhAccountNumber = mergedResponse.linkedRedHatAccountId;
          info.rhProductList = mergedResponse.vendorOfferings
        } 
    
        analytics.track('MarketplaceConnectEvent', info);
      } catch {

        console.error('failed to track'+info)
      }
      finally{

        const encodedData = encodeURIComponent(JSON.stringify(accountDetails));
        const redirectUrl = redirect + `?data=${encodedData}`;
        return <Redirect to={redirectUrl} />;
      }
    }
    
    if (!mergedResponse) {
      return <Spinner size="xl" aria-label="Waiting on order content to load" />;
    }

    const { redHatAccountDetails, marketplaceDetails, vendorOfferings, terms } = mergedResponse;
    const { isAgree, submitting } = this.state;

    return (
      <>
        <PageSection isFilled={true} variant="light" className="pf-u-p-2xl">
          {/* TODO update to use localization */}
          <Title headingLevel="h1" className="pf-u-pb-xl">
            Complete your account connection
          </Title>
          <DescriptionList
            isHorizontal
            horizontalTermWidthModifier={{
              default: '12ch',
              sm: '17ch',
              md: '17ch',
              lg: '17ch',
              xl: '17ch',
              '2xl': '17ch',
            }}
            className="pf-u-pb-2xl"
          >
            <DescriptionListGroup>
              <DescriptionListTerm>{this.props.local('review-link-account.accountNumberLabel')}</DescriptionListTerm>
              <DescriptionListDescription>
                {redHatAccountDetails.accountNumber
                  ? redHatAccountDetails.accountNumber
                  : this.props.local('review-link-account.defaultRedHatAccountNumber')}
              </DescriptionListDescription>
            </DescriptionListGroup>
            {new PartnerOrderDetails(partnerId, marketplaceDetails, local).getRows().map((row, i) => (
              <DescriptionListGroup key={i}>
                <DescriptionListTerm>{row.label}</DescriptionListTerm>
                <DescriptionListDescription>{row.value}</DescriptionListDescription>
              </DescriptionListGroup>
            ))}
            <DescriptionListGroup>
              <DescriptionListTerm>{this.props.local('review-link-account.subscriptionLabel')}</DescriptionListTerm>
              <DescriptionListDescription>{vendorOfferings?.[0]?.description}</DescriptionListDescription>
            </DescriptionListGroup>
          </DescriptionList>
          <Form>
            {/* Embedded component for handling terms, need to remove when empty */
            this.state.hideTerms ||
            <TermsFormGroup
              local={local}
              terms={terms}
              onAgreeToTerms={(agree) => this.setIsAgree(agree)}
              onSelectTermsId={(id) => this.setTermsPdfId(id)}
            />
            }
            <ActionGroup>
              <Button variant="primary" type="button" isDisabled={!isAgree || submitting} onClick={() => this.linkAccounts()}>
                {this.props.local('review-link-account.connectAccountButtonLabel')}
              </Button>
              <Button variant="link" href="/" component="a" style={{ paddingTop: '6px' }}>
                {this.props.local('review-link-account.cancelButtonLabel')}
              </Button>
            </ActionGroup>
          </Form>
        </PageSection>
      </>
    );
  }

  setMergedResponse(mergedResponse) {
    this.setState({ mergedResponse: mergedResponse });
  }

  setRedirect(redirect) {
    this.setState({ redirect: redirect });
  }

  setIsAgree(isAgree) {
    this.setState({ isAgree: isAgree });
  }

  setTermsPdfId(termsPdfId) {
    this.setState({ termsPdfId: termsPdfId });
  }

  setSubmitting(submitting) {
    this.setState({ submitting: submitting });
  }

  setFetchingResponse(fetchingResponse) {
    this.setState({ fetchingResponse: fetchingResponse });
  }

  fetchMergedResponse() {
    if (this.state.mergedResponse === null && !this.state.fetchingResponse) {
      console.debug('Starting to fetch entitlement details.');

      this.setFetchingResponse(true);
      getPartnerOrderDetailsAsync(this.props.partnerId, this.abortController.signal)
        .then((res) => {
          console.debug('Fetched merged response: ', JSON.stringify(res.data));

          // If it's a 200 response but the body says the request wasn't successful
          // call the error callback and make it look like an axios error, for compatibility
          if (typeof res?.data?.successful === 'boolean' && !res.data.successful) {
            this.errorCallback({ response: res });
          } else {
            this.initializeMergedResponse(res.data);
          }
        })
        .catch((error) => {
          this.errorCallback(error);
        })
        .finally(() => this.setFetchingResponse(false));
    }
  }

  initializeMergedResponse(data) {
    if (data.linkedRedHatAccountId) {
      insights.chrome.auth.getUser().then((user) => {
        if (user.identity.org_id === data.linkedRedHatAccountId) {
          this.sendToLandingPage(data);
        } else {
          this.setRedirect(this.errorPaths().alreadyLinked());
        }
      });
    }

    this.setMergedResponse(data);

    //cleaner to just set state here since we have to update isAgree to enable the connect button
    if(data.terms === null || data.terms === undefined || data.terms.length < 1){
      this.setState({isAgree: true, hideTerms: true})
    } else {
      this.setTermsPdfId(getDefaultTerms(data.terms).id);
    }
  }

  componentDidMount() {
    // start fetching when rendered
    console.debug('Starting to fetch order details');
    this.fetchMergedResponse();
  }

  componentWillUnmount() {
    console.debug('Cancelling ongoing requests...');
    this.abortController.abort();
  }

  linkAccounts() {
    if (!this.state.submitting) {
      const { partnerId } = this.props;
      const { termsPdfId } = this.state;

      this.setSubmitting(true);

      linkPartnerOrderToAccountAsync(partnerId, termsPdfId)
        .then((res) => this.onLinkSuccess(res))
        .catch((err) => this.errorCallback(err))
        .finally(() => this.setSubmitting(false));
    }
  }

  onLinkSuccess(success) {
    if (success) {
      // show success then redirect
      this.props.useToast({
        key: 'confirm-link',
        message: this.props.local(`${this.props.partnerId}:toast-notification.confirm-link.success`),
        type: 'success',
      });

      try{
        analytics.track('MarketplaceConnectEvent', {
          outcome:'link-success',
          partner: this.props.partnerId,
          rhAccountNumber:this.state.mergedResponse?.redHatAccountDetails?.accountNumber,
          rhProductList:this.state.mergedResponse?.vendorOfferings
        });      
      } catch {
        console.error('failed to track'+JSON.stringify(this.state.mergedResponse));
      
      } finally{
        this.sendToLandingPage(this.state.mergedResponse);
      }

    } else {
      this.setRedirect(this.errorPaths().error());
    }
  }

  sendToLandingPage(data) {
    let vendorOfferings = data?.vendorOfferings;
    let landingPage = vendorOfferings?.[0]?.landingPage || window['runConfig']['hrefConfig'];

    console.debug('Sending to ' + landingPage);
    window.location.assign(landingPage);
  }

  errorPaths() {
    return getStratosphereErrorPaths(this.props.partnerId);
  }

  errorCallback(error) {
    const errorPaths = this.errorPaths();
    console.debug('Encountered error on call: ', JSON.stringify(error));

    if (error.reason === 'AbortError' || error.message === 'canceled') {
      // do not redirect if aborted
      return;
    }

    if (error?.response?.status === 401) {
      this.setRedirect(errorPaths.unauthorized());
    }

    switch (error?.response?.data?.reason) {
      case 'terms_error':
        this.setRedirect(errorPaths.error());
        break;
      case 'export_compliance_failure':
        this.setRedirect(errorPaths.exportError());
        break;
      case 'linked_different_account': // fall-through
      case 'linked_same_account':
        this.setRedirect(errorPaths.orderError());
        break;
      case 'missing_seller':
      case 'missing_token':
      case 'invalid_token':
      case 'expired_token':
        this.setRedirect(errorPaths.tokenError());
        break;
      default:
        this.setRedirect(errorPaths.systemError());
    }
  }
}

ReviewLinkAccount.propTypes = {
  partnerId: PropTypes.string.isRequired,
  local: PropTypes.func.isRequired,
  useToast: PropTypes.func.isRequired,
};

export default (props) => {
  const {analytics} = useChrome(); 
  return (<ReviewLinkAccount analytics={analytics} partnerId={props.partnerId} local={props.local} useToast={props.useToast}/>)}


