import axios from 'axios';

const getApiUrl = () => {
  const { apiUrl } = window['runConfig'] || {
    apiUrl: 'https://access.stage.redhat.com/subscriptions/link/api/',
  };
  return apiUrl;
};

const getPartnerOrderDetailsAsync = async (partnerId, signal) => {
  return insights.chrome.auth.getToken().then((token) => {
    return axios.get(getApiUrl() + `${partnerId}/details`, {
      headers: { Authorization: `Bearer ${token}` },
      withCredentials: true, // required to supply cookies on cross-site requests
      signal: signal,
    });
  });
};

const linkPartnerOrderToAccountAsync = async (partnerId, termsPdfId) => {
  console.debug(`Linking partner=${partnerId}, terms=${termsPdfId}`);

  return insights.chrome.auth.getToken().then((token) => {
    return axios
      .post(
        getApiUrl() + `${partnerId}/link`,
        {},
        {
          headers: { Authorization: `Bearer ${token}` },
          params: {
            termsPdfId: termsPdfId,
            linkAllOrders: true, // no longer optional
          },
          withCredentials: true, // required to supply cookies on cross-site requests
        }
      )
      .then((res) => res?.data?.linkSuccessful);
  });
};

export { getPartnerOrderDetailsAsync, linkPartnerOrderToAccountAsync };
