import PropTypes from 'prop-types';
import React from 'react';
import ReviewLinkAccount from '../review-link/review-link-account';
import { Route, Switch } from 'react-router-dom';
import ErrorNotification from '../stratosphere-notifications/error-notification';
import getStratosphereErrorPaths from '../stratosphere-error-paths/get-stratosphere-error-paths';

class StratosphereApp extends React.Component {
  static propTypes = {
    match: PropTypes.object.isRequired,
    useToast: PropTypes.func.isRequired,
    local: PropTypes.func.isRequired,
  };

  constructor(props) {
    super(props);
  }

  getPartnerId() {
    return this.props.match.params.partnerId;
  }

  getDecodedData(props) {
    const encodedData = new URLSearchParams(props.location.search).get('data');
    const decodedData = encodedData ? JSON.parse(decodeURIComponent(encodedData)) : {};
    console.log('getDecodedData : ', decodedData);
    return decodedData;
  }

  render() {
    const partnerId = this.getPartnerId();
    const { local, useToast } = this.props;

    // create a version of the i18next "t" function named local that
    // includes the partnerId as the namespace within the options
    const localWithPartner = (key, options) =>
      local(key, {
        ...options,
        ns: partnerId,
      });

    const errorPaths = getStratosphereErrorPaths(partnerId);

    return (
      <Switch>
        <Route exact path={'/' + partnerId}>
          <ReviewLinkAccount partnerId={partnerId} local={localWithPartner} useToast={useToast} />;
        </Route>
        <Route path={errorPaths.tokenError()}>
          <ErrorNotification local={localWithPartner} type="token-error" />
        </Route>
        <Route path={errorPaths.exportError()}>
          <ErrorNotification local={localWithPartner} type="export-error" />
        </Route>
        <Route path={[errorPaths.error(), errorPaths.orderError(), errorPaths.systemError()]}>
          <ErrorNotification local={localWithPartner} type="general-error" />
        </Route>
        <Route path={errorPaths.needAssistance()}>
          <ErrorNotification local={localWithPartner} type="need-assistance" />
        </Route>
        <Route
          path={errorPaths.alreadyLinked()}
          render={(props) => {
            return <ErrorNotification accountDetails={this.getDecodedData(props)} local={localWithPartner} type="already-linked" />;
          }}
        />
      </Switch>
    );
  }
}

export default StratosphereApp;
